header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* socials */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* image */

.me {
    width: 12rem;
    display: block;
    margin: auto;
    left: calc(50% - 11rem);
    margin-top: 3rem;
}

img {
    border-radius: 2rem;
}

/* scroll down */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* media queries (medium devices) */

@media screen and (max-width: 1024px) { 
    
}

/* media queries (small devices) */

@media screen and (max-width: 600px) { 
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }
}